.App {
  text-align: center;
  font-family: Arial;
}

.selected {
  background: greenyellow !important;
}

.options {
  padding: 8px;
  border: 3px solid #000;
  cursor: pointer;
  width: 100%;
  background-color: white;
  color: steelblue;
  font-weight: bold;
}

ul {
  list-style-type: none;
}

ul li {
  margin: 5px;
}

body {
  display: flex;
  justify-content: center;
  font-family: "Segoe UI";
  font-size: 18px;
}

.kupione {
  background-color: greenyellow;
}

.usun {
  background-color: aqua;
}


.input-item{
border-radius: 10px;
  width: 50%;
  font-size: 14px;
  height: 40px;
  padding: 5px;


}

.input-data{
  border-radius: 10px;
  margin-top: 10px;
  width: 50%;
  font-size: 14px;
  height: 40px;
  margin-bottom: 10px;
  padding: 5px;

}

.input-button{
  border-radius: 10px;
  padding: 15px;
  background-color: darkslateblue;
  color: aliceblue;
  font-size: 15px;
  font-weight: 700;
  width: 55%;
}
